@import './fonts.scss'; //Шрифты
@import './nullstyle.scss'; // Нормализация
@import '/src/styles/variables.scss'; //Переменные и миксины 

body {
    font-family: iviSans,sans-serif;
    font-weight: normal;
    font-style: normal;
    background-color: #100e19;
}
.container {
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 42px;
    padding-right: 42px;
}

.heading {
    margin: 50px 0;
    color: $headingColor;
    font-size: 34px;
    font-weight: 600;
    text-align: center;
    //z-index: 1;
    @include hover-colorAnim($textColor, $headingColor)
}
.empty{
    height: 750px;
}
