@import '/src/styles/variables.scss';

.headerMenuTv {
    padding: 10px 28px 20px;
    height: 370px;
    display: flex;
    justify-content: space-between;
    &__hidden {
        display: none;
    }
    &__block {
        min-width: 180px;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        &_slider {
            justify-content: flex-start;
        }
    }
    &__column {
        min-width: 180px;
    }
    &__heading {
        margin-top: 20px;
        font-weight: 500;
        color: $headingColor;
        &_slider {
            padding-bottom: 20px;
        }
    }
    &__list {
        display: flex;
    }
    &__link {
       text-decoration: none; 
    }
    &__item {
        margin-top: 8px;
        width: 200px;

        line-height: 18px;
        color: $textColor;
        
        @include hover-colorAnim ($headingColor, $textColor)
    }
    &__slider {
        padding: 0 20px;
    }
}