.resetFilters {
    &__content {
        cursor: pointer;
        display: flex;
        align-items: center;
        max-width: 180px;

        font-size: 15px;
        line-height: 20px;
        font-weight: 500;
        height: 32px;
        color: #FFFFFF;
        padding: 8px;
        margin-top: 16px;

        &_opacity {
            display: flex;
            align-items: center;

            font-size: 15px;
            line-height: 20px;
            font-weight: 500;
            height: 32px;
            color: rgba(217, 215, 224, .8);
            padding: 8px;
            margin-top: 16px;
            opacity: .32;
        }

        &:hover {
            color: #ea003d;

            .resetFilters__cross {
                fill: #ea003d;
                stroke: #ea003d;
            }
        }
    }

    &__cross {
        margin-right: 5px;
        margin-top: -2px;
        stroke: #FFFFFF;
        stroke-width: 3;
    }
}